* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: 'SegoeUI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

.banner-box, .banner-home {
  min-height: 200px;
  width: 100%;
}

.first-section {
  min-height: 200px;
  color: white;
  text-align: center;
}

.banner-box {
  background-color: rgba(0, 25, 62, 0.5);
}

.banner-home {
  position: absolute;
  background: url("/images/criando-site-html5-css3.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.banner-box > h1 {
  padding: 0.75em 0 0 0;
}

.banner-box > p {
  padding: 0 2em 0 2em;
  margin: -0.75em 0 1.5em 0;
}

button {
  background-color: #666666;
  color: white;
  cursor: pointer;
  padding: 0.3em 2em;
  border: none;
  font-size: 1.1428em;
  display: inline-block;
  transition: all 0.1s ease-in-out;
}

button:hover {
  background-color: white;
  color: #666666;
  border: none;
  transform: scale(1.05);
}

button:active {
  transform: scale(0.95);
}

input[type="text"], input[type="email"], button[type="submit"], textarea {
  width: 95%;
}

input[type="text"], input[type="email"] {
  background-position: 1.3571em 1em;
  background-repeat: no-repeat;
}

input[type="text"], input[type="email"], button[type="submit"] {
  height: 2.8571em;
  padding-left: 3.4em;
  padding-top: 0;
}

input[type="text"] {
  background-image: url("/fonts/icon/nome.png");
}

input[type="email"] {
  background-image: url("/fonts/icon/email.png");
}

textarea {
  height: 14.2857em;
  padding-left: 0.5714em;
  padding-top: 0.8571em;
  resize: vertical;
  min-height: 14.2857em;
}

button[type="submit"] {
  background-color: #ff3f34;
  color: white;
}

.skill {
  display: block;
  width: 100%;
  height: auto;
}

@font-face {
  font-family: 'SegoeUI';
  src: url("/fonts/segoeui/SegoeUI.eot?#iefix") format("embedded-opentype"), url("/fonts/segoeui/SegoeUI.woff") format("woff"), url("SegoeUI.ttf") format("truetype"), url("/fonts/segoeui/SegoeUI.svg#SegoeUI") format("svg");
  font-weight: normal;
  font-style: normal;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 1em 0;
  text-align: center;
}

h1 {
  font-size: 1.71429em;
}

h2 {
  font-size: 1.57143em;
}

h3 {
  font-size: 1.42857em;
}

h4 {
  font-size: 1.28571em;
}

h5 {
  font-size: 1.14286em;
}

h6 {
  font-size: 1em;
}

.main-container {
  max-width: 320px;
}

body {
  background-color: white;
  color: #666666;
}
